input {
  width: 210px;
  height: 30px;
  margin: 10px;
  padding: 5px;
  border-color: white;
  border-style: solid;
  border-radius: 20px;
  box-shadow: -5px 15px 30px grey;
}

button {
  width: 218px;
  height: 38px;
  margin: 10px;
  padding: 5px;
  background-color: tomato;
  border: unset;
  border-radius: 20px;
  margin-top: 20px;
  box-shadow: -5px 15px 30px grey;
}

button:hover {
  opacity: 0.5;
}

