.loginform {
  position: absolute;
  left: calc(50% - 200px);
  top: calc(50% - 200px);
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 400px;
  height: 400px;
  /* border: 1px solid black; */
  box-shadow: -lmanumandu5px 15px 30px grey;
}

.registerform {
  position: absolute;
  left: calc(50% - 200px);
  top: calc(50% - 300px);
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 400px;
  height: 400px;
  /* border: 1px solid black; */
  box-shadow: -lmanumandu5px 15px 30px grey;
}

.profile {
  position: absolute;
  left: calc(50% - 200px);
  top: calc(50% - 300px);
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 400px;
  height: 400px;
  /* border: 1px solid black; */
  box-shadow: -lmanumandu5px 15px 30px grey;
}

input {
  width: 210px;
  height: 30px;
  margin: 10px;
  padding: 5px;
  border-color: white;
  border-style: solid;
  border-radius: 20px;
  box-shadow: -5px 15px 30px grey;
}

button {
  width: 218px;
  height: 38px;
  margin: 10px;
  padding: 5px;
  background-color: tomato;
  border: unset;
  border-radius: 20px;
  margin-top: 20px;
  box-shadow: -5px 15px 30px grey;
}

button:hover {
  opacity: 0.5;
}
